.unselectable {
  -webkit-touch-callout: none;
  -webkit-user-select: none;
  -khtml-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
}
.draw-graph:focus {
  outline: none;
}
.node {
  cursor: pointer;
}
.edge {
  cursor: pointer;
}
.draw-graph-container {
  display: grid;
  width: 1300px;
  grid-template-columns: minmax(100px, 1fr) 200px 700px minmax(100px, 1fr);
  grid-template-rows: 500px 50px;
  background-color: #7ac7c4;
  margin: auto;
  margin-top: 20px;
  border: 0px solid #393e46;
  border-radius: 10px;
  box-shadow: rgba(0, 0, 0, 0.56) 0px 22px 70px 4px;
  padding: 1rem;
  padding-top: 2rem;
  color: #384259;
}

.draw-graph {
  cursor: crosshair;
  box-shadow: rgba(0, 0, 0, 0.35) 0px 5px 15px;
  width: 900px;
  height: 500px;
  margin: 0px auto auto;
  grid-column: 2/4;
  grid-row: 1/2;
  background-color: white;
}

.instructions {
  grid-column: 1/2;
  grid-row: 1/2;
  text-align: left;
  margin: 0 10px 0 0;
  height: 500px;
  box-sizing: border-box;
  border-radius: 4px;
  line-height: 1.5em;
  padding: 10px 10px 5px 10px;
  background-color: #c4edde;
  box-shadow: rgba(0, 0, 0, 0.24) 0px 3px 8px;
}
.instructions h2 {
  text-align: center;
  color: black;
  margin-top: 0.5rem;
  font-family: 'Bree Serif', serif;
  font-family: 'Open Sans', sans-serif;
}
.instructions li {
  margin-bottom: 1rem;
  list-style: none;
  position: relative;
  padding: 0 0 5px 20px;
}

.instructions li::before {
  content: '';
  position: absolute;
  left: 0;
  top: 8px;
  background: #7ac7c4;
  width: 10px;
  height: 10px;
  text-align: center;
  transform: rotate(0deg);
}

.instructions li::after {
  content: '';
  position: absolute;
  top: 8px;
  left: 0;
  height: 10px;
  width: 10px;
  background: #7ac7c4;
  transform: rotate(45deg);
}

.edit-weight-form {
  border-radius: 3px;
  position: absolute;
  background-color: #7ac7c4;
  display: grid;
  grid-template-columns: max-content 20px;
  grid-template-rows: 25px;
  align-items: center;
  padding: 0;
}
.edit-weight-form input {
  height: 15px;
  margin-left: 5px;
  margin-right: 3px;
  width: 60px;
  border-style: solid;
  border-width: 0px;
  border-color: white;
  background-color: #c4edde;
  border-radius: 2px;
  text-align: center;
}
.edit-weight-form input:focus {
  outline: none;
}
.edit-weight-form button {
  margin: 3px;
  height: 17px;
  border-style: none;
  border-radius: 2px;
  padding: 0;
  font-size: 12.5px;
  color: 'white';
  background-color: #f73859;
}
